export const api = ({ method, args = {}, freeze=true }) =>
    new Promise((resolve, reject) =>
        frappe.call({ method, args, freeze, callback: resolve })
    );

 export const fetch = ({ method, args = {} }) =>
    new Promise((resolve, reject) =>
        frappe.call({ method, type:'GET', args, callback: resolve })
    );

export const saveFormConfiguration = ({
        name = "",
        configuration = "",
        department = "",
    }) =>
    api({
        method: "clinical.api.forms.form_builder.save_form_configuration",
        args: {
            name,
            configuration,
            department,
        },
    }).then(({ message }) => message);

export const getFormConfiguration = ({ name = "" }) =>
    fetch({
        method: "clinical.api.forms.form_builder.get_form_configuration",
        args: {
            name,
        },
    }).then(({ message }) => message);

